import { Component, ViewChild } from '@angular/core';
import { ToastComponent, ToastService } from 'ngx-weui';
import { SidebarComponent } from "ngx-weui/sidebar";
import { DataService } from '../datas/data';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  _status: boolean = false;
  mode: string = 'slide';
  position: string = 'left';
  backdrop: boolean = true;
  data;
  currentData;

  constructor(private ds:DataService){
    this.data = ds.getData();
    this.currentData = this.data[this.data.length - 1];
  }

  choose(d){
    this.currentData = d;
  }

  toggleOpened(): void {
    this._status = !this._status;
  }

  openStart() {
    console.log('openStart');
  }

  opened() {
    console.log('opened');
  }

  closeStart() {
    console.log('closeStart');
  }

  closed() {
    console.log('closed');
  }
}



