            title: '第二章：连战',
            content: `        
                <p>                    狂风骤雨，原也可避，但此时却让人避无可避。黑夜的丛林中，约二百人马正扎营休息，说是扎营，实则就只是寻得树林茂密之处暂时歇脚罢了。</p><p>                    营地中心，一人正来回踱步，此人弯腰勾背面目紧拧，一边踱步一边又嘟囔着些什么。突然，他好像不耐烦似的大声喊叫：“这什么狗屁天气，烦死老子了，快来人！给我生火烤肉吃！”</p><p>                    周边士兵听言连忙遵命，去寻找一些湿的不算太透的树枝烧火。</p><p>                    一旁几十步距离处的一颗树下，半蹲半倚着三人。其中两人执长剑，一人背弯弓，此刻正半闭双眼，养精蓄锐。远处传来的喊叫声，也不自觉的传入三人耳中。其中一个威武汉子睁开眼睛，不屑地说道：“哼，这样的酒囊饭袋也能领军，朝廷当真是没救了。”此人正是临冬！</p><p>                    一旁半倚着的英俊，微睁眼皮，却没搭话，只是转着眼球瞄向背弓的乌鸦。乌鸦果然也醒神应和道：“哼，谁让他是那狗官的小舅子呢。打赢了能得个赏赐，打不赢就怪士兵不够精良。这都是他们的惯用手段了，为了混朝廷拨发的银子，就可以牺牲众多士卒，当真可恶至极！”。</p><p>                    临冬不忿地道：“山贼狡猾，不与我们正面交战，奈何你我有通天的本事也使不出来，真叫人难受！”。</p><p>                    “快了。”英俊的声音突然响起。</p><p>                    二人皆看向一旁淡然自若的英俊，开口问道：“何意？”。</p><p>                    英俊起身活动了一下僵硬的脖子，缓缓道：“山贼狡猾，既然知道我们人数众多，必然会选择在夜晚我们人困马乏的时候发动进攻。而这个酒囊饭袋此刻却偏要点火烤肉，这无异于将主帅的位置直接暴露给敌人，所以我说他们快来了。”。二人细想均点头赞同，便不再搭话，却默默加强了戒备。</p><p>                    酒过三巡，菜过五味。那领军之人抚着鼓胀的肚子，甚是愉快，伸了个懒腰便要转身回帐。</p><p>                    就在此时，火光冲天而起！只见营地北面，突然冲出一队骑兵，以迅雷之势直冲大营，只瞬间，毫无防备的士卒便有十余名倒在敌人的马蹄之下。</p><p>                    那刚刚还耀武扬威的领军之人，此刻却有如丧家之犬，只顾慌忙逃窜，鞋帽早就不知丢到哪里去了。正在此时，一道白光闪过，那领军之人便圆睁着双眼，倒在血泊之中。</p><p>                    一众军士尽皆大乱，纷纷四散奔逃，哪还有半点抵抗之心！</p><p>                    “哼，只三十人便杀的这帮酒囊饭袋丢兵弃甲，当真窝囊，我们什么时候出手？”临冬不忿地说道。一旁的乌鸦也斜眼望向英俊，仿佛十分期待英俊发出攻击的命令。</p><p>                    英俊眼望前方，嘴角微微翘起，唇齿开合间吐出一个字：“上”。</p><p>                    此刻的营地中，一队山贼正大肆屠杀，面对毫无抵抗的敌人，他们也自觉无趣，只得加快手中刀剑的速度，尽快结束战斗。那山贼队伍中，有一头戴红巾之人，赤着上身，面目凶狠，貌如豺狼，一身筋肉极为紧实，一看便知是这队山贼中的带头之人。这人手中环刀极为刚猛，所劈之处，兵卒铠甲尽皆崩裂，血肉翻飞！杀伐间毫无惧色，当真匪气冲天！</p><p>                    此人正奋力挥砍，只听得耳边嗖嗖之声略过，身边三名山贼竟然中箭倒地，三支羽箭几乎同时分别射在三人的脖子之上，可见此人拉弓之快，箭法之精准均惊世骇俗！</p><p>                    红巾之人连忙抬刀护住要害，猛然抬眼望去，远处暗中竟杀出三人，两人手执长剑，一人擎弓搭箭，只瞬间便杀到近前！红巾之人气急，怒吼一声便策马挥刀上前，只见前方三人中突然窜出一人，此人面貌刚正威严，来势直如狮虎猛兽，大步执剑而上，便与红巾之人战到一处！两人一人马上，一人马下，拆招换式，兵刃舞得声声作响，刀剑撞击摩擦之声只震得人心神俱散！</p><p>                    其余二人，竟不参战，只顾击杀其他贼兵，那二人功夫之强却也丝毫不弱于这威猛的汉子，真乃好汉也！</p><p>                    方战至十余回合，红巾之人居然力竭！而那汉子却仿佛正打的舒服，一招一式虎虎生风，丝毫没有拖泥带水，招式刚猛却不乏精妙！又过得二三回合，红巾汉子竟汗如雨下，虎口发麻！眼见不敌，红巾汉子只好虚晃一刀，待对手躲闪之际，拨转马头便疾驰逃将而去。而其余贼兵则尽数覆灭，此战告捷！</p><p>                    “哼，我还没用力气，他却跑了。”，临冬将长剑收回腰间，对着赶来的二人说道。英俊则大笑道：“临兄果然好武艺！”，说罢又看向左旁董毓赞道：“乌鸦兄弟，没想到你的箭法居然如此精湛”。二人答了句谬赞，便也收拾兵刃，观察起营地的情况。</p><p>                    “唉，这些兵丁，还未战便已跑了大半，只剩得这五十余人，当真可悲可叹。”乌鸦在一旁略有感触地说道。</p><p>                    英俊眼中却无失望之色，言道：“塞翁失马焉知非福，残余兵士虽少，但贵在精良，不是贪生怕死之辈，与此等弟兄为伍，我等才更应该放心。”言罢，英俊突然举起主营前的令旗，高声呼喊道：“弟兄们，如今主帅战死，贼兵必然以为得胜，所谓骄兵必败！可有谁愿意跟随我兄弟三人，趁敌不备，杀入敌营，连战之！”，余下兵丁尽是一些胆识俱佳的好汉，遂尽举兵刃，同喝道：“愿往！”。</p><p>                    “好！今夜与我三兄弟同战之人，都是好兄弟，待我们取了贼首，得之赏赐共享之！”，众人皆士气大振！</p><p>                    战备完毕，三人分别取得一杆银头红缨枪，一杆铁头大戟，一把乌木直弓，又挑得三匹快马。挑的趁手的兵刃，众人信心更足，如箭在弦，整装待发。</p>
            `
