            title:'第七章：赵黄金',
            content:`
            <p>                岁月如梭，一转眼，已到年底。北境成立已然数月，点点雪花飘落。</p><p>                “又下雪了。”英俊感慨道，此刻四人正围坐于英俊府内。</p><p>                临冬拿起手边烫好的酒壶，为其他三人斟满酒，言道：“是啊，数月之前，你我还是素未相识的陌生人，谁又会想到短短数月，你我竟成了生死与共的兄弟，还建立了北境。”</p><p>                四人饮尽杯中温酒，复又斟满。龙文言道：“但主公确实履行了诺言，翻修道路，兴建土木，施恩于百姓，招得精兵良将，福泽一方，着实是个好君主！”</p><p>                乌鸦附言道：“不错，主公主张办学，兴建学堂。又命士兵闲时帮助百姓农耕，如今虽已入冬，但农户们都存好了种子和余粮，想必来年定会有个好收成！现在百姓们私下都夸赞主公乃明公也。”</p><p>                英俊苦笑道：“只薄待了三位兄弟，只能于我陋室内，喝这酸酒。”</p><p>                三人大笑，临冬言道：“主公不必介怀，这酒虽酸，但也是我百姓子民亲酿，想必过些年后，有了好粮食，这酒却变成佳酿也说不定！”</p><p>                龙文点头赞同：“临冬兄弟说的极是，如若你我不顾百姓死活，自顾贪图享受，与那县官又有何区别？如今主公贤明，乃百姓之福，更是我等之福！”</p><p>                四人只言尽于此，一腔热情，都在那杯杯温酒之中。</p><p>                此刻城外远处，却有两人骑马立于风雪之中，二人服侍华贵，腰中配剑，但却面带怒色。其中一个四十余岁之人，头戴青冠，冠上写着一个大字“忍”。此人此刻很是郁闷，因为他有难言之隐。只因他家中正掀起一场风暴，原来此人正是那黄金军首赵黄金，江湖人称“荆州六六六”。</p><p>                那他有何难言之隐呢？原来前些日子他的原配妻子竟趁他与第九个小妾缠绵之际，与自己的部下将军做了羞耻的事情。黄金没有选择原谅她，挥刀斩了淫妇，那将军却将他暴打了一顿，逃之夭夭。</p><p>                这个家丑很快就在周边地区传扬开来，黄金军北方有诸侯烈风玄甲军来信嘲讽，两家本就有土地资源之纠纷，赵黄金怒极，即刻发兵攻之，大败而归！手下谋士劝其遇事多隐忍，并为其缝制青色纱帽上绣忍字，意为遇事多忍，常常原谅。赵黄金时常佩戴以提醒自己。</p><p>                数月前赵黄金便听得民间传言，说此地出了一个大英雄，斩杀朝廷狗官，为民除害，并自建势力，号北境。赵黄金最信不得什么英雄，心高气也傲。此时他亲自前来打探时，方气的吹胡子打眼。大骂道：“这北境也太过分了，数月间竟然把疆土扩大到我的地盘上了，居然不通知我这个主人，他是什么意思？”</p><p>                一旁的年轻人言道：“主公，北境只占得区区一个县城，便如此嚣张，如今竟然欺辱到我们头上，当真可恶，恐怕他是没听说过主公的名号吧！”</p><p>                赵黄金气的差点没抽过去，大声喝道：“你个蠢材，我说了多少次了，那名号不是什么好意，都是一帮邪魔外道起来挖苦我的，不能当真，不能当真！”</p><p>                年轻人强忍笑意，半晌，正色道：“归根结底，这北境也是侵占我黄金军的土地，不若主公修书一封，以示警告，想必以北境如今的微弱实力，还是会畏惧我们三分的。”</p><p>                那赵黄金却挥手道：“去去去，看你那龟儿子样，出的什么狗屁主意，我赵黄金最受不了的就是别人不尊重我，今天北境强占我土地，明天我便也抢回来，且看他能拿我如何。”</p><p>                年轻人无奈道：“我要是龟儿子？您岂不是老王八了！您就是越这样，别人才越不尊重你，您怎么就不明白呢，枉您还是娶了八个小妾之人，阅历怎还这么短浅？”</p><p>                赵黄金挥手就要抽他，嚷道：“你丫，滚...滚...滚...”。</p><p>                次日，英俊四兄弟正在府衙内办理公事，忽有士兵前来禀报，说城外有部队强行驱赶北境农户，毁坏房屋和农耕用具，那军队还赫然举着黄金军大旗。</p><p>                英俊连忙召集几兄弟前来相商，龙文大怒道：“又是这个赵黄金，当日之仇，我正愁无处可报，他却自己送上门来。主公，拨与我一千兵马，我去取他首级回来”</p><p>                英俊抬手言道：“莫急，龙将军。这赵黄金此举必有目的，他只驱逐我北境农户，说明可能我们与他有土地纠纷。此等事情，只需互通书信便可，他却直接发兵，足见此人不成大器，不足为虑。”</p><p>                龙文急道：“可赵黄金如此过分，我们若不做出行动，怕是会伤了民心！”</p><p>                英俊笑道：“龙将军无需着急，我听闻北方有烈风玄甲军，素来与赵黄金不合，我北境实力如今尚弱，兵卒数量甚少，要想攻入黄金军的城池之中，恐怕需要借助烈风玄甲军的力量。”</p><p>                乌鸦道：“听闻烈风玄甲军，全城尚武，那此去外交必遭刁难。不可派普通官吏去也，乌鸦毛遂自荐想去试试深浅。”</p><p>                英俊大悦道：“好，乌鸦文武双全，又沉着冷静，若肯亲自出马，必定大有见效。你且到库房挑得精良宝剑数把，再领良驹百匹，以充礼数。”</p><p>                赵黄金房中，美酒佳肴，笙箫竹乐，此刻正乐得逍遥，还是那赵黄金父子俩。此时均已喝的酩酊大醉，赵黄金大喝道：“北境也不过如此嘛，想必已经被我军威吓怕了吧。有胆跟我赵黄金抢地盘，就怕你没胆守！哈哈哈！”</p><p>                赵黄金的儿子也满嘴胡话道：“老哥六六六。”</p><p>                黄金气的绿帽一歪，骂道：“六你妹，什么老哥，我是你爹！”</p>
            `
